    <template>
    	<div>
    	  <button @click="isOpen = !isOpen">
    	    My Profile
    	  </button>
      
        <transition
          @before-enter="beforeEnter"
          @enter="enter"
          @leave="leave"
          :css="false"
        >
          <div v-if="isOpen" class="drawer">
            <img src="../assets/avatar.jpg" alt="avatar" />
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </transition>
      </div>
    </template>

    <script>
    
    export default {
      name: 'drawer',
      data() {
        return {
          isOpen: false
        }
      },
      methods: {
         beforeEnter(el) {
      el.style.opacity = 0
      el.style.width = '0em'
    },

         enter(el, done) {
      Velocity(
        el, // element to animate
        { opacity: 1, width: '12em' }, // new style rules
        { duration: 1000, easing: 'easeOutCubic', complete: done } // define how transition happens and complete it
      )
    },

    enter(el, done) {
      Velocity(
        el,
        { opacity: 1, width: '12em' }, 
        { duration: 1000, easing: [60, 10], complete: done } // now with spring physics
      )
    }


      }
    }
    </script>

    <style scoped>
    img {
      height: 2.5em;
      width: 2.5em;
      border-radius: 50%;
    }
    
    .drawer {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 12em;
      height: 20em;
      border-radius: 1%;
      background-color: #e0e0e0;
      box-shadow: 0.08em 0.03em 0.4em #ababab;
      padding-top: 0.7em;
    }
    .drawer div {
      height: 3.5em;
      width: 95%;
      margin-top: 0.6em;
      background-color: #f0f0f0;
      border: 0.02em solid #ababab;
      border-radius: 1%;
    }
    </style>



